'use client'

import { useState, useEffect, useRef } from 'react'
import Link from 'next/link'
import { useRouter } from 'next/navigation'
import { Search, Menu, X, Loader2, Sun, Moon } from 'lucide-react'
import { searchAnime, Anime } from '@/lib/api'

export function Navbar() {
  const router = useRouter()
  const [isOpen, setIsOpen] = useState(false)
  const [mobileSearchOpen, setMobileSearchOpen] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [results, setResults] = useState<Anime[]>([])
  const [loading, setLoading] = useState(false)
  const [isDark, setIsDark] = useState(false)
  const dropdownRef = useRef<HTMLDivElement>(null)
  const abortControllerRef = useRef<AbortController | null>(null)

  // Initialize theme
  useEffect(() => {
    const html = document.documentElement
    const dark = localStorage.theme === 'dark' || (!localStorage.theme && window.matchMedia('(prefers-color-scheme: dark)').matches)
    if (dark) html.classList.add('dark')
    setIsDark(dark)
  }, [])

  const toggleTheme = () => {
    const html = document.documentElement
    if (isDark) {
      html.classList.remove('dark')
      localStorage.theme = 'light'
      setIsDark(false)
    } else {
      html.classList.add('dark')
      localStorage.theme = 'dark'
      setIsDark(true)
    }
  }

  // Fetch search results with debounce
  useEffect(() => {
    if (!searchQuery.trim()) {
      setResults([])
      setLoading(false)
      return
    }

    if (abortControllerRef.current) abortControllerRef.current.abort()
    const controller = new AbortController()
    abortControllerRef.current = controller
    setLoading(true)

    const timeoutId = setTimeout(async () => {
      try {
        const res = await searchAnime(searchQuery, 1, controller.signal)
        if (!controller.signal.aborted) {
          setResults(res.data?.slice(0, 6) || [])
          setLoading(false)
        }
      } catch (err: any) {
        if (err.name !== 'AbortError') console.error(err)
        setResults([])
        setLoading(false)
      }
    }, 500)

    return () => {
      clearTimeout(timeoutId)
      controller.abort()
    }
  }, [searchQuery])

  // Close dropdown when clicking outside
  useEffect(() => {
    const listener = (e: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(e.target as Node)) {
        setResults([])
      }
    }
    document.addEventListener('mousedown', listener)
    return () => document.removeEventListener('mousedown', listener)
  }, [])

  const handleSubmitSearch = (q?: string) => {
    const finalQuery = q || searchQuery
    if (!finalQuery) return
    setResults([])
    setMobileSearchOpen(false)
    router.push(`/search?q=${encodeURIComponent(finalQuery)}`)
  }

  return (
    <>
      <nav className="sticky top-0 z-50 bg-white/95 dark:bg-gray-900/95 backdrop-blur border-b border-gray-200 dark:border-gray-700">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            {/* Logo */}
            <Link href="/" className="flex items-center gap-2 group">
              <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center neon-glow">
                <span className="text-white font-bold text-lg">F</span>
              </div>
              <span className="text-xl font-bold text-gray-900 dark:text-white hidden sm:inline group-hover:text-primary transition-colors">
                FeinimeList
              </span>
            </Link>

            {/* Desktop Menu */}
            <div className="hidden md:flex items-center gap-8">
              {['Home','Trending','Seasonal','Popular','Genres'].map(link => (
                <Link key={link} href={`/${link.toLowerCase()}`} className="text-gray-900 dark:text-white hover:text-primary transition-colors text-sm font-medium">{link}</Link>
              ))}
            </div>

            {/* Desktop Search */}
            <div className="relative hidden sm:flex items-center gap-2 bg-gray-100 dark:bg-gray-800 border border-gray-300 dark:border-gray-700 rounded-lg px-3 py-2 w-64 focus-within:ring-1 focus-within:ring-primary transition-all">
              <Search size={18} className="text-gray-400 dark:text-gray-400" />
              <input
                type="text"
                placeholder="Search anime..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                onKeyDown={(e) => e.key === 'Enter' && handleSubmitSearch()}
                className="bg-transparent outline-none text-sm w-full placeholder:text-gray-400 dark:placeholder:text-gray-500 dark:text-gray-200"
              />
              {loading && <Loader2 size={16} className="animate-spin text-primary" />}

              {results.length > 0 && (
                <div ref={dropdownRef} className="absolute top-full left-0 w-full mt-2 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-700 rounded-md shadow-xl overflow-hidden z-50 animate-in fade-in slide-in-from-top-2">
                  {results.map(anime => (
                    <Link
                      key={anime.mal_id}
                      href={`/anime/${anime.mal_id}`}
                      onClick={() => { setResults([]); setSearchQuery('') }}
                      className="flex items-center gap-3 px-3 py-2 w-full text-left hover:bg-primary/10 dark:hover:bg-primary/20 transition-colors border-b border-gray-200 dark:border-gray-700 last:border-0"
                    >
                      <img src={anime.images.jpg.image_url} alt={anime.title} className="w-10 h-14 object-cover rounded" />
                      <div className="flex-1 min-w-0">
                        <p className="text-sm font-medium truncate">{anime.title}</p>
                        <p className="text-xs text-gray-500 dark:text-gray-400">{anime.type} • {anime.episodes ?? '?' } eps</p>
                      </div>
                    </Link>
                  ))}
                  <button 
                    onClick={() => handleSubmitSearch()}
                    className="w-full text-center text-xs font-bold text-primary bg-gray-100 dark:bg-gray-700 py-2 hover:bg-gray-200 dark:hover:bg-gray-600 transition-colors"
                  >
                    View all results for "{searchQuery}"
                  </button>
                </div>
              )}
            </div>

            {/* Right (Theme + Mobile) */}
            <div className="flex items-center gap-2">
              <button onClick={toggleTheme} className="p-2 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-700 transition-colors">
                {isDark ? <Sun size={20} /> : <Moon size={20} />}
              </button>

              <button className="sm:hidden p-2 hover:bg-gray-200 dark:hover:bg-gray-700 rounded-lg transition-colors" onClick={() => setMobileSearchOpen(true)}>
                <Search size={24} />
              </button>
              <button className="md:hidden p-2 hover:bg-gray-200 dark:hover:bg-gray-700 rounded-lg transition-colors" onClick={() => setIsOpen(!isOpen)}>
                {isOpen ? <X size={24} /> : <Menu size={24} />}
              </button>
            </div>
          </div>

          {/* MOBILE MENU */}
          {isOpen && (
            <div className="md:hidden mt-2 space-y-2 px-2 pb-4 animate-in slide-in-from-top-5">
              {['Home','Trending','Seasonal','Popular','Genres'].map(link => (
                <Link key={link} href={`/${link.toLowerCase()}`} className="block px-3 py-2 rounded-md hover:bg-gray-200 dark:hover:bg-gray-700">{link}</Link>
              ))}
            </div>
          )}
        </div>
      </nav>

      {/* FULLSCREEN MOBILE SEARCH */}
      {mobileSearchOpen && (
        <div className="fixed inset-0 bg-white/95 dark:bg-gray-900/95 backdrop-blur-lg z-[999] p-4 animate-in fade-in duration-200">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-gray-900 dark:text-white">Search Anime</h2>
            <button onClick={() => { setMobileSearchOpen(false); setResults([]); setSearchQuery('') }} className="p-2 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-700">
              <X size={26} />
            </button>
          </div>

          <div className="flex items-center gap-3 bg-gray-100 dark:bg-gray-800 border border-gray-300 dark:border-gray-700 rounded-xl px-4 py-3 focus-within:ring-2 focus-within:ring-primary/50 transition-all">
            <Search size={20} className="text-gray-400 dark:text-gray-400" />
            <input
              autoFocus
              type="text"
              placeholder="Search anime..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyDown={(e) => e.key === 'Enter' && handleSubmitSearch()}
              className="bg-transparent outline-none text-base w-full placeholder:text-gray-400 dark:placeholder:text-gray-500 dark:text-gray-200"
            />
            {loading && <Loader2 className="animate-spin text-primary" size={20} />}
          </div>

          <div className="mt-4 space-y-2 max-h-[70vh] overflow-y-auto">
            {results.length === 0 && searchQuery && !loading && (
              <p className="text-center text-gray-500 dark:text-gray-400 mt-10">No results found.</p>
            )}
            {results.map(anime => (
              <Link
                key={anime.mal_id}
                href={`/anime/${anime.mal_id}`}
                onClick={() => { setMobileSearchOpen(false); setResults([]); setSearchQuery('') }}
                className="flex items-center gap-3 p-2 rounded-lg hover:bg-primary/10 dark:hover:bg-primary/20 w-full text-left transition animate-in slide-in-from-bottom-2"
              >
                <img src={anime.images.jpg.image_url} alt={anime.title} className="w-12 h-16 object-cover rounded" />
                <div>
                  <p className="font-medium line-clamp-1">{anime.title}</p>
                  <p className="text-xs text-gray-500 dark:text-gray-400">{anime.type} • {anime.episodes ?? '?'} eps</p>
                </div>
              </Link>
            ))}
            {results.length > 0 && (
              <button onClick={() => handleSubmitSearch()} className="w-full py-3 mt-2 text-center text-primary font-bold border border-primary/20 rounded-lg hover:bg-primary/10">
                See all results
              </button>
            )}
          </div>
        </div>
      )}
    </>
  )
}
